.header {
    padding: 40px 40px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #404040;
    background-color: #FFF;
    transition: background-color .3s ease, color .3s ease;
}
.header__title {
    margin: 0;
    font-weight: normal;
}
